@tailwind base;
@tailwind components;
@tailwind utilities;
.widget.widget_icl_lang_sel_widget {
  @apply absolute right-8 top-12;
}
.widget.widget_icl_lang_sel_widget .wpml-ls-legacy-dropdown {
  @apply w-auto;
}
.widget.widget_icl_lang_sel_widget .wpml-ls-legacy-dropdown ul .wpml-ls-link, .widget.widget_icl_lang_sel_widget .wpml-ls-legacy-dropdown ul .wpml-ls-item-toggle {
  @apply border-0 bg-[#F4F4F4] p-0;
}
.widget.widget_icl_lang_sel_widget .wpml-ls-legacy-dropdown ul .wpml-ls-link::after, .widget.widget_icl_lang_sel_widget .wpml-ls-legacy-dropdown ul .wpml-ls-item-toggle::after {
  display: none !important;
}
.widget.widget_icl_lang_sel_widget .wpml-ls-legacy-dropdown ul .wpml-ls-link img, .widget.widget_icl_lang_sel_widget .wpml-ls-legacy-dropdown ul .wpml-ls-item-toggle img {
  @apply w-8 h-8 rounded-xl;
}

.page-template-page-language-selection .main-content {
  @apply w-full h-screen mx-auto flex items-center justify-center;
}
.page-template-page-language-selection .main-content ul .wpml-ls-item .wpml-ls-link {
  @apply mb-6;
}
.page-template-page-language-selection .main-content ul .wpml-ls-item .wpml-ls-link .wpml-ls-flag {
  @apply rounded-lg object-cover h-[75px];
}

.page-template-page-links {
  @apply bg-[#F4F4F4];
}
.page-template-page-links .main-content {
  @apply w-full mx-auto max-w-2xl h-screen;
}
.page-template-page-links .main-content .menu-links--item .button {
  box-shadow: 0px 4px 4px 0px rgba(0, 0, 0, 0.03);
  @apply flex justify-between items-center bg-white rounded-md text-blue font-serif text-xl leading-none font-light tracking-[0.07875rem] uppercase whitespace-nowrap break-normal py-5 px-4 mb-3 transition-all duration-500 ease-in-out hover:text-gold;
}
.page-template-page-links .main-content .menu-links--item .button .download-icon path {
  @aplly fill-blue transition-all duration-500 ease-in-out;
}
.page-template-page-links .main-content .menu-links--item .button:hover .download-icon path {
  @aplly fill-gold;
}
.page-template-page-links .main-content .menu-links--item .button .button-icon-wrapper {
  @apply flex gap-x-6 items-center;
}
.page-template-page-links .main-content .menu-links--item .button .button-icon-wrapper .icon {
  @apply w-7 h-7 flex items-center justify-center;
}